$font-family: Roboto-Regular
$font-MarckScript-Regular: MarckScript-Regular
$font-Sofia-Regular: Sofia-Regular

@font-face
  font-family: "SofiaRegular"
  src: local("SofiaRegular"), url("../assets/fonts/Sofia-Regular.ttf") format("truetype")
  font-weight: normal

@font-face
  font-family: "ItaliannoRegular"
  src: local("ItaliannoRegular"), url("../assets/fonts/Italianno-Regular.ttf") format("truetype")
  font-weight: normal

@font-face
  font-family: "YesteryearRegular"
  src: local("YesteryearRegular"), url("../assets/fonts/Yesteryear-Regular.ttf") format("truetype")
  font-weight: normal

@font-face
  font-family: "MrDeHavilandRegular"
  src: local("MrDeHavilandRegular"), url("../assets/fonts/MrDeHaviland-Regular.ttf") format("truetype")
  font-weight: normal

@mixin text_reset
  padding: 0
  margin: 0

.title
  @include text_reset
  &__main
    text-align: center
    letter-spacing: 6px
    font-size: min(24px, 5vw)
    font-weight: 700
    text-transform: uppercase
    line-height: 2
    color: $notification-color
    margin-bottom: min(50px, 10vw)
  &__menu
    margin: 20px 0 50px

.subtitle
  @include text_reset
  &__link
    letter-spacing: 3px
    line-height: 40px
    text-decoration: none
    color: $primary-black
    &_active
      font-weight: bold
