$primary-white: #ffffff
$primary-black: #000000
$secondary-color: #2c3b62
$third-color: #ee4065
$notification-color: #0c3483
$disabled-color: #aaaaaa
$success-color: #00ff7b
$error-color: #ff0000
$bg-color: #fafdf3

#root[data-theme="light"]
    --background-header-color: rgba(245, 245, 245, 1)
    --background-modal-color: rgba(245, 245, 245, 1)
    --background-color: rgba(255, 117, 56, 1)
    --text-color: rgba(29, 30, 51, 1)

#root[data-theme="dark"]
    --background-header-color: rgba(29, 30, 51, 1)
    --background-modal-color: rgba(29, 30, 51, 1)
    --background-color: rgba(29, 30, 51, 1)
    --text-color: rgba(255, 189, 136, 1)